 *{
      padding: 0px;
      margin: 0px;
      font-family:Arial, Helvetica, sans-serif;
 }
.header{

        width: 100%;
        height: 30%;
        background-color: rgb(33, 114, 149);
}
.nav-bar{

    display: flex;
    justify-content:center;
    /* flex-wrap: wrap; */
}
.nav{

  display:flex;
  padding: 5px;
  margin: 3px;
}
.nav-item{

     list-style: none;
     background-color:burlywood;
     padding: 5px;
     margin:13px;
     border-radius: 4px;
}
a{

     text-decoration: none;
     font-size: large;
     color: rgb(15, 14, 13);
     padding: 4px;
     margin: 3px; 
     font-weight: bold;    
}
.form{

    width: 50%;
    height: 60vh;
    background-color: darkblue;
    margin: 2px auto;
}
input{

    width: 70%;
    margin: 13px 0px 0px 13px;
    padding:12px;
    border-radius: 4px;
}
label{

   margin: 0px 0px 0px 4px;
   font-size: 20px;
   color:white;
}
.mytext{

  margin-top: 22px;
  /* width: 20px; */
}
h1{

  /* margin-bottom: 40px; */
  text-align: center;
   font-size: 20px;
   padding: 10px;
   color: white ;

}
.btn{

  margin:15px auto 0 20px;
  padding: 12px;
  border-radius: 4px;
  font-size: 18px;
  background-color: black;
  color: white;
}
container{

   width: 250px;
   background-color:rebeccapurple;
   margin:0px 0px 0px 25%;
}
h2{

  color: white;
}

.container{

     margin: 0px 0px 0px 25%;
}
.pk{

